import './SideBar.scss'
import Friends from '../../assets/1.png'
import Groups from '../../assets/2.png'
import Market from '../../assets/3.png'
import Watch from '../../assets/4.png'
import Memories from '../../assets/5.png'
import Events from '../../assets/6.png'
import Gaming from '../../assets/7.png'
import Gallery from '../../assets/8.png'
import Videos from '../../assets/9.png'
import Messages from '../../assets/10.png'
import Toturials from '../../assets/11.png'
import Courses from '../../assets/12.png'
import Funds from '../../assets/13.png'
import { AuthContext } from '../../Context/authContext'
import { useContext } from 'react'

const SideBar = () => {
  const {currentUser} = useContext(AuthContext)
  return (
    <div className='leftbar'>
      <div className="container">
        <div className="menu">
          <div className="user">
          <img src={currentUser.img}alt="user"/>
          <span>{currentUser.name}</span>
          </div>
          <div className="item">
            <img src={Friends} alt="" />
            <span>Friends</span>
          </div>
          <div className="item">
            <img src={Groups} alt="" />
            <span>Groups</span>
          </div>
          <div className="item">
            <img src={Market} alt="" />
            <span>Market</span>
          </div>
          <div className="item">
            <img src={Watch} alt="" />
            <span>Watch</span>
          </div>
          <div className="item">
            <img src={Memories} alt="" />
            <span>Memories</span>
          </div>
         
        </div>
        <hr/>
        <div className="menu">
          <span>Your Shortcus here</span>
          <div className="item">
            <img src={Events} alt="" />
            <span>Events</span>
          </div>
          <div className="item">
            <img src={Gaming} alt="" />
            <span>Gaming</span>
          </div>
          <div className="item">
            <img src={Gallery} alt="" />
            <span>Gallery</span>
          </div>
          <div className="item">
            <img src={Videos} alt="" />
            <span>Videos</span>
          </div>
          <div className="item">
            <img src={Messages} alt="" />
            <span>Messages</span>
          </div>
          
        </div>
        <hr/>
        <div className="menu">
          <span>Other</span>
          <div className="item">
            <img src={Toturials} alt="" />
            <span>Toturials</span>
          </div>
          <div className="item">
            <img src={Courses} alt="" />
            <span>Courses</span>
          </div>
          <div className="item">
            <img src={Funds} alt="" />
            <span>Funds</span>
          </div>
          
        </div>
      </div>

    </div>
  )
}

export default SideBar